.taskDetails {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  max-width: 858px;
  z-index: 9999;
  overflow-y: auto;
  background: #ffffff;
  box-shadow: 0px 5px 20px rgba(21, 27, 38, 0.08);
  transition: right 0.5s ease;
}
